import React, { useContext } from 'react';
import { MeasurementSystem, SharedContext } from './SharedContext';
import { TableCell, TableContainer, TableRow, Table, TableHead, TableBody } from '@material-ui/core';

const FirstTab: React.FC = () => {
  const context = useContext(SharedContext);

  if (!context) {
    return null;
  }

  const {
    measurementSystem,
    drawdownLength,
    setDrawdownLength,
    drawdownInsideDiameter,
    setDrawdownInsideDiameter,
    startPressure,
    setStartPressure,
    finishPressure,
    setFinishPressure,
    zevacD,
    setZevacD,
    zevacM,
    setZevacM,
    zevacT,
    setZevacT,
    zevacH,
    setZevacH,
    cyclePerMinute,
    setCyclePerMinute,
    unitEfficiency,
    setUnitEfficiency
  } = context;

  return (
    <div>
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Input</TableCell>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Value</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            <TableRow>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Drawdown Length {measurementSystem === MeasurementSystem.Imperial ? '(ft.)' : '(m.)'}</TableCell>
              <TableCell>
                <input
                  type="number"
                  value={drawdownLength}
                  onChange={(e) => setDrawdownLength(Number(e.target.value) * (measurementSystem === MeasurementSystem.Imperial ? 1 : 3.28084))}
                  placeholder="Enter Drawdown Length value"
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Drawdown Inside Diameter {measurementSystem === MeasurementSystem.Imperial ? '(ft.)' : '(m.)'}</TableCell>
              <TableCell>
                <input
                  type="number"
                  value={drawdownInsideDiameter}
                  onChange={(e) => setDrawdownInsideDiameter(Number(e.target.value) * (measurementSystem === MeasurementSystem.Imperial ? 1 : 3.28084))}
                  placeholder="Enter Drawdown Inside Diameter value"
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Start Pressure {measurementSystem === MeasurementSystem.Imperial ? '(PSIG)' : '(PA)'}</TableCell>
              <TableCell>
                <input
                  type="number"
                  value={startPressure}
                  onChange={(e) => setStartPressure(Number(e.target.value) / (measurementSystem === MeasurementSystem.Imperial ? 1 : 6894.76))}
                  placeholder="Enter Start Pressure value"
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Finish Pressure {measurementSystem === MeasurementSystem.Imperial ? '(PSIG)' : '(PA)'}</TableCell>
              <TableCell>
                <input
                  type="number"
                  value={finishPressure}
                  onChange={(e) => setFinishPressure(Number(e.target.value) / (measurementSystem === MeasurementSystem.Imperial ? 1 : 6894.76))}
                  placeholder="Enter Finish Pressure value"
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Recovery Compressor: ZEVAC-D</TableCell>
              <TableCell>
                <input
                  type="number"
                  value={zevacD}
                  onChange={(e) => setZevacD(Number(e.target.value))}
                  placeholder="Enter ZEVAC-D value"
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Recovery Compressor: ZEVAC-M</TableCell>
              <TableCell>
                <input
                  type="number"
                  value={zevacM}
                  onChange={(e) => setZevacM(Number(e.target.value))}
                  placeholder="Enter ZEVAC-M value"
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Recovery Compressor: ZEVAC-T</TableCell>
              <TableCell>
                <input
                  type="number"
                  value={zevacT}
                  onChange={(e) => setZevacT(Number(e.target.value))}
                  placeholder="Enter ZEVAC-T value"
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Recovery Compressor: ZEVAC-H</TableCell>
              <TableCell>
                <input
                  type="number"
                  value={zevacH}
                  onChange={(e) => setZevacH(Number(e.target.value))}
                  placeholder="Enter ZEVAC-H value"
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Cycle Per Minute</TableCell>
              <TableCell>
                <input
                  type="number"
                  value={cyclePerMinute}
                  onChange={(e) => setCyclePerMinute(Number(e.target.value))}
                  placeholder="Enter Cycle per Minute value"
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell style={{fontFamily: 'Montserrat, sans-serif'}}>Unit Efficiency</TableCell>
              <TableCell>
                <input
                  type="number"
                  value={unitEfficiency}
                  onChange={(e) => setUnitEfficiency(Number(e.target.value))}
                  placeholder="Enter Unit Efficiency value"
                />
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default FirstTab;