import React, { useState, useContext } from 'react';
import { TableContainer, Table, TableHead, TableCell, TableRow, TableBody, Paper } from '@material-ui/core';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import FirstTab from './FirstTab';
import { calculateGasAtFinish, calculateGasAtStart, calculateHoursDrawdownTime } from './SecondTab';
import ToggleMetricImperial from './ToggleMetricImperial';
import { SharedContext, MeasurementSystem } from './SharedContext';

interface CalculatorProps { };

const Calculator: React.FC<CalculatorProps> = () => {
  const [selectedTab, setSelectedTab] = useState(0);

  const context = useContext(SharedContext);

  if (!context) {
    return null;
  }

  const {
    measurementSystem,
  } = context;

  const handleTabChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setSelectedTab(newValue);
  }

  return (
    <div>
      <ToggleMetricImperial />
      <p>Recovery Impact:</p>
      <div>
        <TableContainer component={Paper} style={{ border: '1px solid red' }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell style={{ textAlign: 'center', fontFamily: 'Montserrat, sans-serif'}}>Hours Drawdown Time</TableCell>
                <TableCell style={{ textAlign: 'center', fontFamily: 'Montserrat, sans-serif'}}>Gas Available at Start ({measurementSystem === MeasurementSystem.Imperial ? 'SCF' : 'SCM'})</TableCell>
                <TableCell style={{ textAlign: 'center', fontFamily: 'Montserrat, sans-serif'}}>Gas Recovered ({measurementSystem === MeasurementSystem.Imperial ? 'SCF' : 'SCM'})</TableCell>
                <TableCell style={{ textAlign: 'center', fontFamily: 'Montserrat, sans-serif'}}>Gas Not Recovered ({measurementSystem === MeasurementSystem.Imperial ? 'SCF' : 'SCM'})</TableCell>
                <TableCell style={{ textAlign: 'center', fontFamily: 'Montserrat, sans-serif'}}>Percent Gas Recovered</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <TableRow>
                <TableCell style={{ textAlign: 'center' }}>{calculateHoursDrawdownTime(context).toLocaleString()}</TableCell>
                <TableCell style={{ textAlign: 'center' }}>{calculateGasAtStart(context).toLocaleString()}</TableCell>
                <TableCell style={{ textAlign: 'center' }}>{(calculateGasAtStart(context) - calculateGasAtFinish(context)).toLocaleString()}</TableCell>
                <TableCell style={{ textAlign: 'center' }}>{calculateGasAtFinish(context).toLocaleString()}</TableCell>
                <TableCell style={{ textAlign: 'center' }}>{(((calculateGasAtStart(context) - calculateGasAtFinish(context)) * 100) / (calculateGasAtStart(context))).toFixed(1)}%</TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </TableContainer>
      </div>
      <p>Summary Impact:</p>
      <div>
        <TableContainer component={Paper} style={{ border: '1px solid red' }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell style={{ textAlign: 'center', fontFamily: 'Montserrat, sans-serif'}}>Natural Gas ({measurementSystem === MeasurementSystem.Imperial ? 'imperial' : 'metric'} tons)</TableCell>
                <TableCell style={{ textAlign: 'center', fontFamily: 'Montserrat, sans-serif'}}>CO2 Gas ({measurementSystem === MeasurementSystem.Imperial ? 'imperial' : 'metric'} tons)</TableCell>
                <TableCell style={{ textAlign: 'center', fontFamily: 'Montserrat, sans-serif'}}>Carbon Credits</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <TableRow>
                <TableCell style={{ textAlign: 'center', color: 'green'}}>{((calculateGasAtStart(context) - calculateGasAtFinish(context)) * 0.0055 * (measurementSystem === MeasurementSystem.Imperial ? 1 : 0.907185)).toLocaleString()}</TableCell>
                <TableCell style={{ textAlign: 'center', color: 'green'}}>{((calculateGasAtStart(context) - calculateGasAtFinish(context)) * 0.0055 * 264.9985763831 * (measurementSystem === MeasurementSystem.Imperial ? 1 : 0.907185)).toLocaleString()}</TableCell>
                <TableCell style={{ textAlign: 'center', color: 'green'}}>{((calculateGasAtStart(context) - calculateGasAtFinish(context)) * 0.0055 * 264.9985763831 * (measurementSystem === MeasurementSystem.Imperial ? 1 : 0.907185)).toLocaleString()}</TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </TableContainer>
      </div>
      <div>
        <Tabs value={selectedTab} onChange={handleTabChange}>
          <Tab label="Pipeline Inputs" style={{ textAlign: 'center', fontFamily: 'Montserrat, sans-serif'}} />
        </Tabs>
        {selectedTab === 0 && <FirstTab />}
      </div>
      <div>
        <h4>
          Disclaimer:
        </h4>
        <h6>
          Statements, data and other information contained on this website that are not historical facts are based on current expectations, estimates, forecasts, opinions and beliefs, and may constitute forward-looking statements, which can be identified by the use of forward-looking terminology such as may, will, should, expect, projected, anticipate, target, estimate, intend, continue, or believe, or other variations thereon or comparable terminology.  Due to various risks and uncertainties, actual events or results or the actual performance may differ materially from those reflected or contemplated in such forward-looking statements.  Nothing contained on this website may be relied upon as a guarantee, promise, assurance or a representation as to the future performance of any project and/or investment.
        </h6>
      </div>
    </div>
  )
};

export default Calculator;
